












import Vue from 'vue';

export default Vue.extend({
  name: 'textfield',
  props: {
     disabled: {
      default: false,
      type: Boolean,
    },
    placeholder: {
      default: 'Type here',
      type: [Number, String],
    },
    value: {
      default: '',
      type: [Number, String],
    },
  },
  methods: {
    updateValue(value: string) {
      this.$emit('input', value);
    },
  },
});
